.wrapper{padding:35px 0 50px}.wrapper form{display:inline-block;margin-bottom:15px;border:1px solid #ccc}.wrapper form input{display:inline-block;text-align:center;border:none;margin:0 5px;width:32px;height:32px;pointer-events:none}.wrapper form input[type=number]::-webkit-inner-spin-button,.wrapper form input[type=number]::-webkit-outer-spin-button{-webkit-appearance:none;margin:0}.header{text-align:center;color:var(--text-title);padding:0 0 30px 0}.header h1{font-size:3rem;margin-bottom:10px}.header p{font-size:1.4rem;margin-bottom:10px}.header::after{content:"";background:#252a2b;display:block;width:60px;height:4px;margin:25px auto 0}.info-prd{margin-bottom:30px;border-bottom:1px solid #ededed}.img-prd img{max-width:100%}.info-text{padding-bottom:20px}.info-text p{margin-bottom:5px;color:var(--black);font-size:1.4rem;text-transform:uppercase}.name-prd{display:flex;align-items:center;justify-content:space-between;margin-bottom:5px}.name-prd h3{text-transform:uppercase;font-size:1.6rem}.name-prd svg{font-size:1.8rem}.name-prd svg:hover{cursor:pointer;color:var(--primary)}.value-button{display:inline-block;margin:0px;text-align:center;vertical-align:middle;width:32px;height:32px;padding:4px 0;font-size:1.4rem;background:#eee;-webkit-touch-callout:none;-webkit-user-select:none;-khtml-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}.value-button:hover{cursor:pointer}.total-money{display:flex;align-items:center;justify-content:space-between;font-size:1.6rem}.total-money span{font-weight:bold}.title{margin-bottom:10px;font-weight:700;font-size:16px;color:#666}.checkout-note textarea{border-radius:0;background:#ededed;color:#000;font-size:15px;padding:20px 15px;outline:none;width:100% !important;min-height:130px;font-weight:500;border:1px solid rgba(0,0,0,0);transition:all 150ms linear;appearance:none}.policy ul li{display:block;line-height:25px;position:relative;padding-left:20px;overflow:hidden}.policy ul li::before{font-family:FontAwesome;font-style:normal;font-weight:normal;text-decoration:inherit;content:"";font-size:10px;left:0;opacity:1;position:absolute}.sidebox-order{background:#fff;padding:15px;position:relative;margin-bottom:30px;border:1px solid #e1e3e4;border-radius:2px}.sidebox-order_title{margin-bottom:24px}.sidebox-order_title h3{font-size:20px;font-weight:bold;margin:10px 0}.sidebox-order_total{font-size:16px;color:#5c5c5c;font-weight:600;padding:15px 0;margin-bottom:24px;border-top:1px dotted #dfe0e1;border-bottom:1px dotted #dfe0e1}.sidebox-order_total p{margin:0}.sidebox-order_total .total-price{float:right;color:red;font-size:24px}.sidebox-order_action .btncart-checkout{border-radius:4px;cursor:pointer;width:100%;font-size:15px;font-weight:400;padding:12px 20px;background-color:var(--secondary);border:1px solid var(--secondary)}.sidebox-order_action .btncart-checkout:hover{border:1px solid var(--secondary);background-color:rgba(0,0,0,0)}.sidebox-order_action .link-continue{margin:14px 0 0;text-align:center}.sidebox-order_action .link-continue a{color:var(--primary)}.sidebox-order_action .link-continue a ion-icon{margin-right:6px}